.h5 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
}

.h5Cards {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.h5Card {
  margin-top: 10px;
  width: 90%;
}

.h5AddCard {
  margin-top: 10px;
  width: 90%;
}

.h5Button {
  margin-top: 10px;
  text-align: center;
  width: 90%;
  height: 40px;
  line-height: 40px;
  border-radius: 10px;
  border: #dfe4ea 1px solid;
  font-size: 18px;
}

.h5ButtonPrimary {
  background: #70a1ff;
  color: #ffffff;
  border-color: #1e90ff;
}

.h5ButtonDisable {
  background: #f5f5f5;
  border-color: #d9d9d9;
  color: rgba(0,0,0,0.25);
}
